html, body {
    max-width: 100%;
    overflow-x: hidden;
}
.main-page-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}
/* central part */
.fashion-container{
    background-color: #d8d8d8;
    width: 100%;
    height:700px;
    position: absolute;
    top: 0;
    left: 0;
}
/* left part */
.art-container{
    background-color: #ededed;
    height: 700px;
    -webkit-clip-path: polygon(0 0, 100% 0, 75% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 75% 100%, 0 100%);
    -webkit-transition: width 1.5s ease;
    -o-transition: width 1.5s ease;
    transition: width 1.5s ease;
}
.music-art{
    height: 600px;
    width: 50%;
}
/* right part */
.music-container{
    background-color: #dcdcdc;
    height: 700px;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
    -webkit-transition: width 1.5s ease;
    -o-transition: width 1.5s ease;
    transition: width 1.5s ease;
}
.music-brace{
    height: 600px;
    width: 50%;
}

/* container which contain left and right part */
.curtains-music-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:700px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;

}
.curtains-art-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:700px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}


.menu-container{
    width: 21%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.menu-container > h2{
margin-bottom: 0;
width: 250px;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;
cursor: pointer;
-webkit-transition: color 1s ease;
-o-transition: color 1s ease;
transition: color 1s ease;
-webkit-animation-delay: 1.5s;
        animation-delay: 1.5s;
font-family: 'Montserrat', sans-serif;
font-weight: bold;
font-size: 28px;

}

.menu-container > p{
margin-top: 0;
width: 250px;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;
-webkit-transition: color 1s ease;
-o-transition: color 1s ease;
transition: color 1s ease;
-webkit-animation-delay: 1.5s;
        animation-delay: 1.5s;
font-family: 'Montserrat', sans-serif;
font-weight: 200;
font-size: 19px;
}

.menu{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 220px;
    list-style-type: none;
    padding-left: 15px;
    cursor: pointer;
    -webkit-transition: color 4s ease;
    -o-transition: color 4s ease;
    transition: color 4s ease;
    margin-top: -1px;
}
.menu > li{
    -webkit-transition: color 1s ease;
    -o-transition: color 1s ease;
    transition: color 1s ease;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}
.menu > li > a:hover{
    opacity: 0.8;
}
.menu > li > a:active{
    opacity: 0.5;
}
.main-menu-item{
    -webkit-transition: color 1s ease;
    -o-transition: color 1s ease;
    transition: color 1s ease;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    font-family: 'Montserrat', sans-serif;
    opacity: 0.8;
}

/* left menu */
.additional-menu{
    position: absolute;
    top:20px;
    left: -62%;
    height: 120px;
    width: 70%;
    background-color: black;
    -webkit-clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
